input[type="checkbox"] {
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 40px;
  height: 16px;
  border: 1px solid #aaa;
  border-radius: 2px;
  background: #ebebeb;
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer; }
  input[type="checkbox"]:after {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    width: 14px;
    height: 14px;
    background: white;
    border: 1px solid #aaa;
    border-radius: 2px;
    -webkit-transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -o-transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    transition: left 0.1s cubic-bezier(0.785, 0.135, 0.15, 0.86); }
  input[type="checkbox"]:checked {
    background: #47525E;
    border-color: #8492A6; }
    input[type="checkbox"]:checked:after {
      left: 23px;
      border-color: #8492A6; }
  input[type="checkbox"]:hover:not(:checked):not(:disabled):after, input[type="checkbox"]:focus:not(:checked):not(:disabled):after {
    left: -1px;
    right: 0px; }
  input[type="checkbox"]:hover:checked:not(:disabled):after, input[type="checkbox"]:focus:checked:not(:disabled):after {
    left: 23px; }
  input[type="checkbox"]:disabled {
    opacity: 0.5; }

.rangeSlider {
  width: 100%; }

.rangeSliderRange {
  -webkit-appearance: none;
  width: calc(100% - (73px));
  height: 10px;
  border-radius: 5px;
  background: #d7dcdf;
  outline: none;
  padding: 0;
  margin: 0; }
  .rangeSliderRange::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #47525E;
    cursor: pointer;
    -webkit-transition: background .15s ease-in-out;
    -o-transition: background .15s ease-in-out;
    transition: background .15s ease-in-out; }
    .rangeSliderRange::-webkit-slider-thumb:hover {
      background: #8492A6; }
  .rangeSliderRange:active::-webkit-slider-thumb {
    background: #8492A6; }
  .rangeSliderRange::-moz-range-thumb {
    width: 20px;
    height: 20px;
    border: 0;
    border-radius: 50%;
    background: #47525E;
    cursor: pointer;
    -webkit-transition: background .15s ease-in-out;
    -o-transition: background .15s ease-in-out;
    transition: background .15s ease-in-out; }
    .rangeSliderRange::-moz-range-thumb:hover {
      background: #8492A6; }
  .rangeSliderRange:active::-moz-range-thumb {
    background: #8492A6; }
  .rangeSliderRange:focus::-webkit-slider-thumb {
    -webkit-box-shadow: 0 0 0 3px #fff, 0 0 0 6px #8492A6;
    box-shadow: 0 0 0 3px #fff, 0 0 0 6px #8492A6; }

.rangeSliderValue {
  display: inline-block;
  position: relative;
  width: 35px;
  color: #fff;
  line-height: 20px;
  text-align: center;
  border-radius: 2px;
  background: #47525E;
  padding: 5px 10px;
  margin-left: 8px; }
  .rangeSliderValue:after {
    position: absolute;
    top: 8px;
    left: -7px;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 7px solid #47525E;
    border-bottom: 7px solid transparent;
    content: ''; }

input::-moz-focus-inner, input::-moz-focus-outer {
  border: 0; }

